.container {
  display: inline-block;
  position: relative;
  z-index: 1;
}
.container:focus {
  outline: none;
}
.menu-container {
  position: absolute;
  background-color: #fff;
  border-radius: 6px;
  padding: 0.5rem 0;
  right: 0;
  box-shadow: 2px 3px 6px 0 rgba(0, 0, 0, 0.1);
}
.menu-contents {
  position: static;
  font-size: 0.9rem;
  text-align: left;
}
.menu-contents a {
  color: #222;
}
.dropdown-row {
  width: 11.5rem;
  padding: 0.2rem 0rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.dropdown-row:hover {
  cursor: pointer;
  background-color: #e1e1e1;
}
.dropdown-row.header .dropdown-icon {
  width: 1rem;
}
.header {
  color: #999;
}
.dropdown-row.header:hover {
  cursor: default;
  background-color: inherit;
}
.dropdown-icon {
  text-align: center;
  flex: 0 0 20px;
}
.dropdown-text {
  text-align: left;
  flex: 1 1 auto;
  /* padding-right:5rem; */
  /* padding: 0.2rem 0.5rem; */
}
.dropdown-right {
  text-align: right;
}
.dropdown-text-active {
  font-weight: 700;
}
